import React, {Component, Fragment} from 'react';
import {Input, Button, Icon} from "antd";
import keycode from 'keycode';

const ButtonGroup = Button.Group;

class TranslateInput extends Component {
  constructor(props) {
    super(props);
    this.state = {
      texts: [],
    };
    document.addEventListener('keydown', this.onkeydown);
  }
  onkeydown = (e) => {
    const cmdKey = e.ctrlKey || e.metaKey;
    const key = keycode(e);
    const accept = ['1', '2', '3', '4', '5'];
    if (cmdKey && accept.indexOf(key) !== -1) {
      e.preventDefault();
      this.cmd(key);
    }
  };
  cmd = (key) => {
    switch (key) {
      case '1': {
        // ctrl + 1 开始/暂停播放
        this.props.playPause();
        break;
      }
      case '2': {
        this.props.backward(2);
        break;
      }
      case '3': {
        this.props.backward(1);
        break;
      }
      case '4': {
        this.props.forward(1);
        break;
      }
      case '5': {
        this.props.forward(2);
        break;
      }
      default: {
        break;
      }
    }
  };
  download = () => {
    const str = this.formatTrans(0);
    const blob = new Blob([str], {type: 'text/plain'});
    this.saveFile(blob, '翻译.txt');
  };
  formatTrans = (rev) => {
    let arr = JSON.parse(JSON.stringify(this.state.texts));
    if (rev) {
      arr.reverse();
    }
    return arr.map(obj => `${obj.time}  ${obj.text}`).join('\n');
  };
  /**
   * 保存文件
   */
  saveFile = (blob, filename) => {
    const saveData = (() => {
      const a = document.createElement('a');
      document.body.appendChild(a);
      a.style = 'display: none';
      return (b, fn) => {
        const url = window.URL.createObjectURL(b);
        a.href = url;
        a.download = fn;
        a.click();
        window.URL.revokeObjectURL(url);
      };
    })();
    saveData(blob, filename);
  };
  onPressEnter = (e) => {
    const { elapsed } = this.props;
    const value = e.target.value;
    const text = value.split('\n');
    const newText = {
      time: elapsed,
      text: text[text.length - 1],
    };
    this.setState(state => ({
      ...state,
      texts: state.texts.concat(newText),
    }));
  };
  render() {
    const {
      playPause,
      playing,
      elapsed,
      total,
    } = this.props;
    return (
      <Fragment>

        <tr>
          <th>工具栏</th>
          <td>

            <table border="1">
              <tbody>

                <tr>
                  <th>cmd + 1</th>
                  <td>
                    <Button
                      size="small"
                      type="primary"
                      onClick={() => this.cmd('1')}
                    >
                      <Icon type={playing ? 'pause' : 'caret-right'} />
                    </Button>
                  </td>
                </tr>

                <tr>

                  <th>cmd + 2</th>
                  <td>
                    <Button
                      size="small"
                      type="ghost"
                      onClick={() => this.cmd('2')}
                    >
                      <Icon type="backward" />2s
                    </Button>
                  </td>

                  <th>cmd + 3</th>
                  <td>
                    <Button
                      size="small"
                      type="ghost"
                      onClick={() => this.cmd('3')}
                    >
                      <Icon type="backward" />1s
                    </Button>
                  </td>

                </tr>
                <tr>

                  <th>cmd + 4</th>
                  <td>
                    <Button
                      size="small"
                      type="ghost"
                      onClick={() => this.cmd('4')}
                    >
                      <Icon type="forward" />1s
                    </Button>
                  </td>

                  <th>cmd + 5</th>
                  <td>
                    <Button
                      size="small"
                      type="ghost"
                      onClick={() => this.cmd('5')}
                    >
                      <Icon type="forward" />2s
                    </Button>
                  </td>



                </tr>
              </tbody>
            </table>

          </td>
        </tr>

        <tr>
          <th>状态</th>
          <td>
            {elapsed}/{total}
          </td>
        </tr>

        <tr>
          <th>翻译</th>
          <td>
            <div>
              <Input.TextArea
                className="translation-input"
                onPressEnter={this.onPressEnter}
              />
            </div>
          </td>
        </tr>

        <tr>
          <th>预览</th>
          <td>
            <div className="output">
              <pre>
                {this.formatTrans(1)}
              </pre>
            </div>
          </td>
        </tr>

        <tr>
          <th>下载</th>
          <td>
            <Button type="ghost" size="small" onClick={this.download}>点击下载翻译文档</Button>
          </td>
        </tr>
      </Fragment>

    );
  }
}

export default TranslateInput;
